import React from 'react';
import anime from 'animejs';
import { Link } from "react-router-dom";
import '../css/custom.css';

class Home extends React.Component {

    introAnimation() {
        var introTimeline = anime.timeline();

        introTimeline
        .add({
            targets: '.pa1',
            strokeDashoffset: [anime.setDashoffset, 0],
            easing: 'easeOutQuad',
            duration: 750,
            delay: anime.stagger(500, {start: 250}),
            direction: 'alternate',
            fill: '#282c34',
        })
        .add({
            targets: '.re1',
            d: [
                { value: "M 510 637 h 5 v 80 h -5 Z" },
                { value: "M 220 637 h 580 v 80 h -580 Z" },
            ],
            easing: 'easeOutCirc',
            duration: 3000,
            loop: false,
        }, "2500")
        .add({
            targets: document.getElementById("re2"),
            d: [
                { value: "M 510 637 h 5 v 80 h -5 Z" },
                { value: "M 800 637 h 5 v 80 h -5 Z" },
            ],
            easing: 'easeOutCirc',
            duration: 3000,
            loop: false,
        }, "2500")
        .add({
            targets: document.getElementById("re3"),
            d: [
                { value: "M 510 637 h 5 v 80 h -5 Z" },
                { value: "M 220 637 h 5 v 80 h -5 Z" },
            ],
            easing: 'easeOutCirc',
            duration: 3000,
            loop: false,
        }, "2500")
        .add({
            targets: document.getElementById("re2"),
            duration: 1000,
            opacity: 0,
            easing: 'easeOutCirc',
        }, "4000")
        .add({
            targets: document.getElementById("re3"),
            duration: 1000,
            opacity: 0,
            easing: 'easeOutCirc',
        }, "4000")
        .add({
            targets: '.hd2',
            duration: 1200,
            fill: "#282c34",
            easing: 'easeInSine',
            delay: anime.stagger(600),
        }, "4500");
    }

    transitionAnimation() {
        var transitionAnimation = anime.timeline();
        console.log("Here!");

        transitionAnimation
        .add({
            targets: ['.hd1','.hd2'],
            easing: 'easeOutQuad',
            duration: 750,
            delay: anime.stagger(500, {start: 250}),
            fill: 'none',
        });
    }

    componentDidMount() {
        this.introAnimation();
    }

    componentWillUnmount() {
        this.transitionAnimation();
    }

    render() {

        return (
            <svg version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 1024">
                <g transform="scale(0.5, -0.5) translate(515, -1100)">
                    <path className="pa1" d="M 532 669 Q 595 682 743 693 Q 753 692 764 705 Q 765 717 742 728 Q 709 749 653 736 Q 599 726 544 713 Q 538 712 536 711 L 485 702 Q 382 692 278 682 Q 247 679 270 660 Q 301 633 347 641 Q 392 650 448 657 L 532 669 Z"></path>
                    <path className="pa1" d="M 531 663 Q 531 667 532 669 L 536 711 Q 539 762 558 794 Q 565 804 553 817 Q 531 835 503 846 Q 487 855 473 849 Q 460 845 465 830 Q 483 802 485 702 L 485 618 Q 479 509 489 491 Q 490 488 493 483 Q 503 467 517 475 Q 527 485 529 568 Q 529 610 530 630 L 531 663 Z"></path>
                    <path className="pa1" d="M 448 657 Q 367 530 153 402 Q 140 392 148 389 Q 157 383 169 387 Q 289 417 419 547 Q 429 559 442 570 Q 479 610 485 618 C 525 665 477 701 448 657 Z"></path>
                    <path className="pa1" d="M 530 630 Q 732 428 787 418 Q 869 418 939 436 Q 958 440 959 444 Q 960 451 946 456 Q 748 517 669 563 Q 627 588 531 663 C 507 681 509 651 530 630 Z"></path>
                    <path className="pa1" d="M 523 317 Q 623 395 657 403 Q 675 409 669 424 Q 666 439 607 473 Q 589 483 569 475 Q 509 453 419 429 Q 395 422 339 424 Q 317 425 325 406 Q 332 394 349 384 Q 376 369 411 388 Q 555 439 568 432 Q 578 426 574 414 Q 543 374 509 327 C 491 303 499 299 523 317 Z"></path>
                    <path className="pa1" d="M 550 278 Q 547 302 529 314 Q 525 317 523 317 L 509 327 Q 472 351 458 353 Q 448 356 448 336 Q 449 321 468 308 Q 490 290 496 271 L 503 236 Q 519 82 495 42 Q 494 41 492 38 Q 486 34 419 42 Q 380 51 379 44 Q 379 38 455 -26 Q 474 -44 489 -67 Q 496 -76 507 -71 Q 520 -64 543 -28 Q 573 30 556 239 L 550 278 Z"></path>
                    <path className="pa1" d="M 556 239 Q 677 248 845 243 Q 879 242 910 241 Q 932 241 938 250 Q 945 263 926 280 Q 868 328 796 313 Q 697 297 550 278 L 496 271 Q 315 253 103 233 Q 79 232 96 211 Q 130 177 175 186 Q 346 228 503 236 L 556 239 Z"></path>
                </g>
                
                <clipPath id="show-name">
                    <path className="re1" d="M 510 1200 h 5 v 80 h -5 Z"/>
                </clipPath>
                <path id="re2" d="M 513 1200 h 5 v 80 h -5 Z"/>
                <path id="re3" d="M 513 1200 h 5 v 80 h -5 Z"/>
                <text className="hd1" x="507" y="69%" textAnchor="middle" clipPath="url(#show-name)">WAYNE</text>
                <Link to="/about">
                    <text className="hd2" x="4%" y="50%" textAnchor="start">about</text>
                </Link>
                <Link to="/contact">
                    <text className="hd2" x="96%" y="50%" textAnchor="end">contact</text>
                </Link>
            </svg>
        )
    }
}

export default Home;